<template>
  <div id="app">
    <router-view v-on:getscore="getscoreup" v-bind:passScore="score"></router-view>
  </div>
</template>

<script>
import weixinApi from '@/assets/weixin'
export default {
  name: 'app',
  data () {
    return {
      score: 0
    }
  },
  methods: {
    getscoreup (score) {
      this.score = score
    }
  },
  created () {
    var wxShare = {
      title: 'title',
      desc: 'miaoshu',
      link: window.location.href,
      imgUrl: 'tupianurl'
    }
    weixinApi.share.init(wxShare)
  }
}
</script>

